<moduleView viewClass="theme-rename-ttl" viewId="themeRenameTtl"><moduleViewForm formClass="theme-rename-ttl__form"><moduleViewTitle titleClass="theme-rename-ttl__form-title" :titleText="theme.rename.ttl.title"></moduleViewTitle><formInput elType="text" elClass="theme-rename-ttl__form-input" elId="themeRenameTtlFormInput" elValue="" :elPlaceholder="theme.rename.ttl.inputPlace" @inputAction="saveInputTtlThemeRename('themeRenameTtlFormInput')"></formInput><mainBtn elType="button" elId="themeRenameTtlBtn" elClass="theme-rename-ttl__form-btn" :elText="theme.rename.ttl.btnSub" @clickAction="changeThemeRenameTtlFormModule().then(() =&gt; { toggleThemeRenameTtlFormModule(), delInfoForRenameTheme(); })"></mainBtn><moduleViewBtnExit btnType="button" btnClass="theme-rename-ttl__form-btn-exit" :btnText="theme.rename.ttl.btnExit" @clickAction="toggleThemeRenameTtlFormModule(), delInfoForRenameTheme()"></moduleViewBtnExit></moduleViewForm></moduleView>