<div class="todolist-profile__menu" id="todolistProfileMenu"><div class="todolist-profile__menu-header"><h5 class="todolist-profile__menu-header-username">{{ webUser.username }}</h5><todoListNavClock></todoListNavClock></div><ul class="todolist-profile__menu-list"><li class="todolist-profile__menu-item"><button class="todolist-profile__menu-btn" type="button" @click="toggleRenameFormModule(), todoMenuActive()">{{ profile.menu.btn.renameTextBtn }}</button></li><li class="todolist-profile__menu-item"><button class="todolist-profile__menu-btn" type="button" @click="toggleChangeAvatarFormModule(), todoMenuActive()">{{ profile.menu.btn.renameImgBtn }}</button></li><li class="todolist-profile__menu-item"><button class="todolist-profile__menu-btn" @click="logout(), todoMenuActive(), loginToRouter()" type="button">{{ profile.menu.btn.exitBtn }}</button></li></ul></div>