<moduleView viewClass="change-avatar" viewId="changeAvatar"><moduleViewForm formClass="change-avatar__form"><moduleViewTitle titleClass="change-avatar__form-title" :titleText="profile.changeAvatar.title"></moduleViewTitle><imageFileInput inputClass="change-avatar__form-file" inputId="changeAvatarFormFile" inputName="changeAvatarFormFile" labelClass="change-avatar__form-file-label" labelId="changeAvatarFormFileLabel" :inputLoad="profile.changeAvatar.inputLoad" :inputFileSucess="profile.changeAvatar.inputFileSucess" :inputFile="profile.changeAvatar.inputFile" @changeAction="preloadingImageAvatar($event)"></imageFileInput><mainBtn elType="button" elId="formRenameBtn" elClass="form-rename__form-btn" :elText="profile.changeAvatar.btnSubmit" @clickAction="changeAvatar()"></mainBtn><moduleViewBtnExit btnType="button" btnClass="change-avatar__form-btn-exit" :btnText="profile.changeAvatar.btnExit" @clickAction="toggleChangeAvatarFormModule(), loadProfileInputFile(), delPreloadImageAvatar($event);"></moduleViewBtnExit></moduleViewForm></moduleView>