<section class="todolist__theme"><div class="container"><div class="todolist__theme-wrap"><h3 class="todolist__theme-title">{{ theme.title }}</h3><div class="todolist__theme-cards"><div class="todolist__theme-cards-item" v-for="(item, index) in theme.themeCards" :key="item" :data-theme="item.idx" :style="{ left: item.style.left, opacity: item.style.opacity, zIndex: item.style.zIndex }"><h4 class="todolist__theme-cards-item-title">{{ item.title }}</h4><div class="todolist__theme-cards-item-img"><img :src="item.imgLoad == 'web' ? item.src : require('@/assets/img/' + item.src)"></div></div><button class="todolist__theme-cards-next arrow" :class="theme.btnDisabled == 'next-theme' ? 'todolist__theme-cards-btn--disabled' : ''" @click="activeThemeNext()"> <arrow></arrow></button><button class="todolist__theme-cards-prev arrow" :class="theme.btnDisabled == 'prev-theme' ? 'todolist__theme-cards-btn--disabled' : ''" @click="activeThemePrev()"> <arrow></arrow></button></div></div></div></section>