<div class="todolist__form"><form class="todolist__form-nav" action="" method="get" @submit.prevent><input class="todolist__form-nav-search" id="todoFormInput" type="text" :placeholder="todoForm.inputPlaceholder" @keyup.enter="getNewRecord()"><mainBtn :elText="todoForm.btnText" elClass="todolist__form-nav-btn" @clickAction="getNewRecord()"></mainBtn></form><hr v-if="personRecord.length != 0"><div class="todolist__form-body"><h3 class="todolist__form-body-title" v-if="personRecord.length == 0">{{ todoForm.title }}</h3><draggable class="todolist__form-body-list list-group" id="list" v-else tag="ul" :list="personRecord" handle=".handle" item-key="name" @end="dragRecordSave()"><template #item="{ element, index }"><li class="todolist__form-body-item"><span>{{ index + 1 + ')' }}</span><span>{{ element.text }}</span><button class="todolist__form-body-drag handle" type="button"><span></span></button><div class="todolist__form-body-del"><buttonDelWithIcon @clickAction="delRecord(index)"></buttonDelWithIcon></div></li></template></draggable></div><hr></div>