<moduleView viewClass="theme-rename-img" viewId="themeRenameTtl"><moduleViewForm formClass="theme-rename-img__form"><moduleViewTitle titleClass="theme-rename-img__form-title" :titleText="theme.rename.img.title"></moduleViewTitle><imageFileInput inputClass="theme-rename-img__form-input" inputId="themeRenameTtlFormFile" inputName="themeRenameTtlFormName" labelClass="theme-rename-img__form-label" labelId="themeRenameTtlFormLabel" :inputLoad="theme.rename.img.inputLoad" :inputFileSucess="theme.rename.img.inputFileSucess" :inputFile="theme.rename.img.inputFile" @changeAction="preloadThemeRenameImgFormModule($event)"></imageFileInput><mainBtn elType="button" elId="themeRenameTtlBtn" elClass="theme-rename-img__form-btn" :elText="theme.rename.img.btnSub" @clickAction="uploadThemeRenameImgFormModule().then(() =&gt; { delThemeRenameImgFormModule($event), toggleThemeRenameImgFormModule(); })"></mainBtn><moduleViewBtnExit btnType="button" btnClass="theme-rename-img__form-btn-exit" :btnText="theme.rename.img.btnExit" @clickAction="toggleThemeRenameImgFormModule(), delInfoForRenameTheme()"></moduleViewBtnExit></moduleViewForm></moduleView>