<ul class="workplace__theme-list"><li class="workplace__theme-list-item" v-for="item in list" :key="item"><div class="workplace__theme-list-img"><img :src="item.imgLoad == 'web' ? item.src : require('@/assets/img/' + item.src)"></div><h5 class="workplace__theme-list-title">{{ item.title }}</h5><div class="workplace__theme-list-del" v-if="remove"><buttonDelWithIcon @clickAction="delTheme(item.id)"></buttonDelWithIcon></div><div class="workplace__theme-list-btn" v-else><mainBtn @clickAction="toggleThemeRenameFormModule(), saveInfoForRenameTheme(item.id)" :elText="rename.themeListRenameBtnText" elClass="workplace__theme-list-btn-rename"></mainBtn></div></li></ul>